import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import portfolio from './assets/portfolio.jpg'
import darkBaseTheme from 'material-ui/styles/baseThemes/darkBaseTheme';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import getMuiTheme from 'material-ui/styles/getMuiTheme';
import AppBar from 'material-ui/AppBar';

// Menu bar Items
import Paper from 'material-ui/Paper';
import Menu from 'material-ui/Menu';
import MenuItem from 'material-ui/MenuItem';
import ScrollableAnchor from 'react-scrollable-anchor'
import { configureAnchors } from 'react-scrollable-anchor'
import { goToAnchor } from 'react-scrollable-anchor'
import Drawer from 'material-ui/Drawer';
import Avatar from 'material-ui/Avatar';

configureAnchors({offset: -90, scrollDuration: 800})
const style = {
  width: '75%',
float: 'right'
};
const styleText = {
  'margin-top': '10%',
'text-align' : 'center'
}

class App extends Component {
  handleAbout = ()=>{
    goToAnchor('about',false)
  }
  handleSkills = ()=>{
    goToAnchor('skills', false)
  }
  handleProjects = ()=>{
    goToAnchor('projects', false)
  }
  handleContact = ()=>{
    goToAnchor('contact', false)
  }

  render() {
    return (
      <MuiThemeProvider muiTheme={getMuiTheme(darkBaseTheme)}>
        <AppBar
          // title="JS"
          // showMenuIconButton={false}
          titleStyle={{textAlign: "center"}}/>
        <Drawer>
          <AppBar showMenuIconButton={false}  titleStyle={{flex:'0'}}>
            <Avatar
              src={portfolio}
              size={150}
              style={{'margin-top':'5%'}}
            />

          </AppBar>
          <MenuItem onClick={this.handleAbout}>About</MenuItem>
          <MenuItem onClick={this.handleSkills}>Skills</MenuItem>
          <MenuItem onClick={this.handleProjects}>Projects</MenuItem>
          <MenuItem onClick={this.handleContact}>Contact</MenuItem>
        </Drawer>

        <div style={style}>
          <ScrollableAnchor id={'about'}>
            <div style={{height:'500px'}}>
              <h1>About</h1>

            </div>
          </ScrollableAnchor>
          <ScrollableAnchor id={'skills'}>
            <div style={{height:'500px'}}>
              <h1>Skills</h1>
            </div>
          </ScrollableAnchor>
          <ScrollableAnchor id={'projects'}>
            <div style={{height:'500px'}}>
              <h1>Projects</h1>
            </div>
          </ScrollableAnchor>
          <ScrollableAnchor id={'contact'}>
            <div style={{height:'600px'}}>
              <h1>Contact</h1>
            </div>
          </ScrollableAnchor>
        </div>
      </MuiThemeProvider>
    );
  }
}

export default App;



